@font-face {
    font-family: "Spacey-Regular";
    src:url("./Spacey-Regular.ttf");
}

.App-header {
  font-family: 'Spacey-Regular';
  text-align: center;
  background-color: #222;
  font-size: 4em;
  padding: 10% 0;
  color: white;
}

@media only screen and (min-width: 880px) {
  .App-header {
    font-size: 12em;
  }
}

.App-body {
  padding-top: 25px;
  padding-left: 10vw;
}

.title {
  font-family: serif;
  font-size: 200%;
}

.veggie {
  font-family: sans-serif;
  font-style: italic;
  text-transform: uppercase;
  font-size: 150%;
  line-height: 1.5;
}

a {
  color: initial;
}

a:hover {
  font-weight: 900;
  color: rgba(0,0,0,0.5);
  -webkit-transition: all 0.1s ease-in-out;
  -o-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}

footer {
  text-align: right;
  text-decoration: none;
  margin-right: 20px;
}


